<template>
  <b-modal
    v-if="isShow"
    id="modalTurnChoose"
    title="Danh sách đợt thanh toán"
    no-close-on-backdrop
    no-fade
    size="lg"
    hide-footer
  >
    <div style="height: 400px; overflow: hidden auto">
      <table class="table table-hover">
        <thead class="table-dark">
          <tr>
            <th class="rounded-start">Tên đợt</th>
            <th>Ngày lập</th>
            <th width="10" class="rounded-end"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in turns" :key="item.turn_id">
            <td>{{ item.turn_name }}</td>
            <td>{{ item.turn_date  | moment("DD/MM/YYYY") }}</td>
            <td>
              <button class="btn btn-primary-soft btn-sm" @click="choose(item)">
                <i class="fa-regular fa-circle-arrow-right"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      
      <NotFoundVue v-if="turns.length == 0"></NotFoundVue>
    </div>
  </b-modal>
</template>

<script>
import axios from "axios";
import NotFoundVue from "./NotFound.vue";
import moment from 'moment';
export default {
  components: {
    NotFoundVue,
  },
  data() {
    return {
      isShow: true,
      turns: [],
    };
  },
  created() {},
  methods: {
    choose(item) {
      this.$emit("chooseTurn", item);
      this.$bvModal.hide("modalTurnChoose");
      this.$toast.success("Thêm đợt thành công");
    },
    show() {
      this.$bvModal.show("modalTurnChoose");
      this.getTurns();
    },
    async getTurns() {
      this.$econtract.showLoading();
      await axios.get(`/turns`).then((response) => {
        this.turns = response.data;
        this.$econtract.hideLoading();
      });
    },
  },
};
</script>
