<template>
  <div id="create-commissions">
    <div class="row">
      <div class="col-8 offset-2">
        <div class="card">
          <div class="card-body">
            <!-- <pre>
              {{ Commission.details }}
            </pre> -->
            <h1 class="fw-bold">{{ asset.title }}</h1>
            <div class="row">
              <div class="col-12">
                <div class="form-group mb-2">
                  <label for="">Đợt thanh toán</label>
                  <div class="input-group">
                    <input
                      placeholder="Chọn đợt thanh toán"
                      type="text"
                      class="form-control"
                      v-model="Commission.turn_name"
                      disabled="true"
                    />
                    <button class="input-group-text" @click="showModalTurn">
                      <i class="fa-solid fa-magnifying-glass"></i>
                    </button>
                    <button
                      class="input-group-text"
                      @click="addTurn"
                      title="Thêm đợt thanh toán"
                    >
                      <i class="fa-solid fa-plus"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6">
                <div class="form-group mb-2">
                  <label for="">Chọn hợp đồng</label>
                  <div class="input-group">
                    <input
                      placeholder="Chọn đợt thanh toán"
                      type="text"
                      class="form-control"
                      v-model="asset.company_name"
                      disabled="true"
                    />
                    <button class="input-group-text" @click="showModalContract">
                      <i class="fa-solid fa-magnifying-glass"></i>
                    </button>
                  </div>
                </div>

                <!-- <button class="btn btn-primary" @click="showModalContract()">
                  Hiển thị modal
                </button>
                <div class="form-group">
                  <label for="">Chọn hợp đồng</label>
                  <select2
                    v-model="asset.contract_id"
                    :options="contracts"
                    :settings="settings"
                  />
                  <select v-model="asset.contract_id" class="form-select">
                    <option value="0">--Chọn hợp đồng--</option>
                    <option
                      v-for="contract in contracts"
                      :value="contract.contract_id"
                      :key="contract.contract_id"
                    >
                      {{ contract.company.company_name }} ({{ contract.contract_total }})
                    </option>
                  </select>
                </div> -->
              </div>
              <div class="col-6">
                <div class="form-group">
                  <label for="">Chọn công tác viên:</label>
                  <select v-model="Commission.partner_id" class="form-select">
                    <option value="0">--Chọn cộng tác viên--</option>
                    <option
                      v-for="partner in partners"
                      :value="partner.partner_id"
                      :key="partner.partner_id"
                    >
                      {{ partner.partner_name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>

            <ul class="list-group mt-2">
              <li class="list-group-item bg-light" v-if="asset.company_name">
                <h6 class="m-0 fw-bold">{{ asset.company_name }}</h6>
              </li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <span class="fw-bold"> Số hợp đồng : </span>
                    {{ asset.contract.contract_number }}/{{
                      asset.contract.contract_number_pattern
                    }}
                  </div>
                  <div class="col-6">
                    <span class="fw-bold"> Ngày hợp đồng : </span
                    >{{ asset.contract.contract_date | moment("DD/MM/YYYY") }}
                  </div>
                </div>
              </li>
              <template v-if="asset.contract.details">
                <li class="list-group-item bg-light">
                  <div class="fw-bold">Danh sách dịch vụ</div>
                </li>

                <li class="list-group-item p-0">
                  <table class="table table-striped table-responsive">
                    <thead>
                      <tr>
                        <th width="10">#</th>
                        <th width="60%">Tên dịch vụ</th>
                        <th>SL</th>
                        <th>Tiền trước thuế</th>
                        <th width="10"></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(detail, index) in asset.contract.details"
                        :key="detail.detail_id"
                      >
                        <td>{{ index + 1 }}</td>
                        <td>{{ detail.detail_content }}</td>
                        <td>{{ detail.detail_quantity }}</td>
                        <td>
                          <strong class="text-danger">{{
                            detail.detail_product_price | formatamout
                          }}</strong>
                        </td>
                        <td>
                          <button
                            class="btn btn-danger-soft btn-xs"
                            @click="removeDetail(index)"
                            title="Xóa dịch vụ"
                          >
                            <i class="fa-regular fa-trash-can"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </li>
              </template>
            </ul>
            <ul class="list-group mt-1">
              <li class="list-group-item">
                <div class="row">
                  <div class="col-4">
                    <div class="form-group">
                      <label for="">Tiền trước thuế:</label>
                      <!-- <input
                        type="text"
                        v-model="asset.product_price"
                        class="form-control text-end"
                      /> -->
                      {{ asset.product_price }}
                      <money
                        v-model.lazy="asset.product_price"
                        v-bind="money"
                        class="form-control text-end"
                      >
                      </money>
                    </div>
                  </div>
                  <div class="col-4">
                    <div class="form-group">
                      <label for="">Phần trăm hoa hồng:</label>
                      <input
                        type="number"
                        v-model.lazy="asset.percent"
                        class="form-control text-end"
                      />
                    </div>
                  </div>
                  <div class="col-4">
                    <div class="form-group">
                      <label for="">Tiền tiền thuế:</label>
                      <!-- <input
                        type="text"
                        v-model="asset.comis_price"
                        class="form-control text-end"
                      /> -->
                      <money
                        v-model.lazy="asset.comis_price"
                        v-bind="money"
                        class="form-control text-end"
                      >
                      </money>
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-group-item">
                <button @click="addCommission()" class="btn btn-primary-soft w-100">
                  <i class="fa-thin fa-circle-plus"></i> Thêm phiếu thanh toán
                </button>
              </li>
            </ul>
            <div class="row">
              <div
                class="col-12 mt-1"
                v-for="(item, index) in filteredComissions"
                :key="item.detail_id"
              >
                <div class="card">
                  <div class="card-header">
                    <div class="d-flex ms-auto">
                      <h5 class="card-title m-0">{{ item.company_name }}</h5>
                      <div class="d-flex ms-auto">
                        <button
                          class="btn btn-danger-soft btn-sm"
                          @click="removeDetailItem(index)"
                        >
                          <i class="fa-regular fa-trash-can"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div class="card-body">
                    <h6 class="text-danger fw-bold">
                      Dịch vụ : {{ item.cate_name }} - {{ item.product_name }}
                    </h6>
                    <div class="form-group">
                      <label for="">Nội dung thanh toán</label>
                      <input
                        type="text"
                        v-model="item.detail_content"
                        class="form-control"
                        placeholder="Nội dung thanh toán"
                      />
                    </div>
                    <div class="row">
                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Doanh thu</label>
                          <!-- <input
                        type="text"
                        v-model="item.revenue"
                        class="form-control"
                        placeholder="Doanh thu"
                      /> -->
                          <money
                            v-model.lazy="item.revenue"
                            v-bind="money"
                            class="form-control text-end"
                          >
                          </money>
                        </div>
                      </div>
                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Số tiền thanh toán</label>
                          <!-- <input
                        type="text"
                        v-model="item.comis_price"
                        class="form-control"
                        placeholder="Số tiền thanh toán"
                      /> -->
                          <money
                            v-model.lazy="item.comis_price"
                            v-bind="money"
                            class="form-control text-end"
                          >
                          </money>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="row mt-1">
              <div class="col-12">
                <div class="card">
                  <div class="card-header">
                    <div class="card-title">Thanh toán</div>
                  </div>
                  <div class="card-body">
                    <div class="form-group">
                      <label for="">Tổng tiền thanh toán:</label>
                      <!-- <input
                        type="number"
                        placeholder="Tổng số tiền thanh toán"
                        v-model="Commission.comis_total"
                        class="form-control"
                      /> -->
                      <money
                        v-model.lazy="Commission.comis_total"
                        v-bind="money"
                        class="form-control text-end"
                      >
                      </money>
                    </div>
                    <div class="form-group">
                      <label for="">Số tiền bằng chữ:</label>
                      <input
                        type="text"
                        placeholder="Tổng số tiền thanh toán bằng chữ"
                        v-model="Commission.price_to_text"
                        class="form-control"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="row mt-2" v-if="Commission.details.length > 0">
              <div class="col-12">
                <button class="btn btn-primary-soft" @click="store()">
                  <i class="fa-solid fa-floppy-disk"></i> Lưu dữ liệu
                </button>
                <!-- <button class="btn btn-secondary ms-1" @click="">Quay lại</button> -->
                <router-link
                  :to="{
                    name: 'commission-index',
                  }"
                  class="btn btn-secondary ms-1"
                  ><i class="fa-solid fa-arrow-left"></i> Quay lại</router-link
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <turnChoose ref="turnChoose" @chooseTurn="chooseTurn"></turnChoose>
    <turnAdd ref="turnAdd" @turnAddSuccess="turnAddSuccess"></turnAdd>
    <modalContractComission
      @chooseContract="chooseContract"
      ref="modalContract"
    ></modalContractComission>
  </div>
</template>

<script>
import axios from "axios";
import turnChoose from "@/extends/ModalTurnChoose.vue";
import turnAdd from "@/extends/ModalAddTurn.vue";
import Select2 from "v-select2-component";
import modalContractComission from "@/extends/ModalContractComission.vue";
import { Money } from "v-money";

export default {
  components: {
    turnChoose,
    turnAdd,
    Select2,
    modalContractComission,
    Money,
  },
  data() {
    return {
      Commission: {
        turn_name: "",
        comis_status: 0,
        turn_id: 0,
        partner_id: 0,
        details: [],
        comis_total: 0,
        price_to_text: "",
        comis_id: 0,
      },
      isUpdate: false,
      asset: {
        title: "Lập phiếu thanh toán",
        contract_id: 0,
        contract: {},
        product_price: 0,
        percent: 0,
        comis_price: 0,
        company_name: "",
      },
      partners: [],
      contracts: [],
      settings: {
        placeholder: "--Hãy chọn dữ liệu--",
        theme: "bootstrap-5",
        width: "100%",
      },
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 0,
        masked: false,
      },

      debounceTimeout: null,
    };
  },
  watch: {
    "asset.contract.details": {
      handler: function (value) {
        // console.log(value);
        // this.asset.product_price = this.totalDetails(this.asset.contract);
      },
    },
    "asset.contract_id": {
      //   handler: function (contract_id) {
      //     this.asset.contract = this.contracts.filter((item) => {
      //       return item.contract_id == contract_id;
      //     })[0];
      //     this.asset.product_price = this.totalDetails(this.asset.contract);
      //   },
    },
    "asset.percent": {
      handler: function (value) {
        this.asset.comis_price = Math.round(this.asset.product_price * (value / 100));
      },
      immediate: false,
      deep: true,
    },
    "Commission.details": {
      handler: function () {
        this.Commission.comis_total = this.commisTotal();
        this.Commission.price_to_text = this.$econtract.doc(this.Commission.comis_total);
      },
      immediate: false,
      deep: true,
    },
  },
  created() {
    if (this.$route.params.id > 0) {
      this.isUpdate = true;
      this.Commission.comis_id = this.$route.params.id;
      this.getCommissions();
      this.asset.title = "Cập nhật phiếu thanh toán";
    }
    this.getContracts();
    this.getPartners();
  },
  computed: {
    filteredComissions() {
      return this.Commission.details.filter((item) => item.isDelete !== 1);
    },
  },
  methods: {
    showModalContract() {
      this.$refs.modalContract.show();
    },
    chooseContract(contract) {
      //   this.asset.contract = this.contracts.filter((item) => {
      //     return item.contract_id == contract_id;
      //   })[0];
      this.asset.contract = contract;
      this.asset.company_name = contract.company.company_name;
      this.asset.product_price = this.totalDetails(contract);
      this.asset.contract_id = contract.contract_id;
    },
    removeDetailItem(index) {
      this.$confirm({
        title: "Xóa dịch vụ",
        message: "Bạn có muốn xóa thanh toán này?",
        button: {
          yes: "Yes",
          no: "No",
        },
        callback: (confirm) => {
          if (confirm) {
            // this.Commission.details.splice(index, 1);
            // this.Commission.details.filter(
            //   (item) => item.detail_id !== this.Commission.details[index].detail_id
            // );
            this.$set(this.Commission.details, index, {
              ...this.Commission.details[index],
              isDelete: 1,
            });

            // console.log(this.Commission.details[index].detail_id);
          }
        },
      });
    },
    removeDetail(index) {
      let self = this;
      this.$confirm({
        title: "Xóa dịch vụ",
        message: "Bạn có muốn xóa dịch vụ này?",
        button: {
          yes: "Yes",
          no: "No",
        },
        callback: (confirm) => {
          if (confirm) {
            self.asset.contract.details.splice(index, 1);
          }
        },
      });
    },
    addCommission() {
      if (
        this.asset.contract.contract_id > 0 &&
        this.asset.percent > 0 &&
        this.asset.comis_price > 0
      ) {
        const contractType =
          this.asset.contract.contract_type === 0 ? "Đăng ký mới " : "Gia hạn";
        this.Commission.details.push({
          company_name: this.asset.contract.company.company_name,
          company_taxcode: this.asset.contract.company.company_taxcode,
          company_address: this.asset.contract.company.company_address,
          comis_percent: this.asset.percent,
          revenue: this.asset.product_price,
          comis_price: this.asset.comis_price,
          contract_number:
            this.asset.contract.contract_number +
            "/" +
            this.asset.contract.details[0].category.pattern_contract,
          contract_date: this.$econtract.formatDate(this.asset.contract.contract_date),
          product_name: this.asset.contract.details[0].product.product_name,
          cate_name: this.asset.contract.details[0].category.cate_name,
          contract_id: this.asset.contract_id,
          detail_content: `${contractType} ${this.asset.contract.details[0].category.cate_name} ${this.asset.contract.details[0].product.product_name}`,
        });
        this.asset.contract = {};
        this.asset.product_price = 0;
        this.asset.percent = 0;
        this.asset.comis_price = 0;
      } else {
        this.$confirm({
          title: "Thông báo",
          message: "Bạn chưa chọn đủ thông tin thanh toán",
          button: {
            yes: "Yes",
          },
          callback: (confirm) => {},
        });
      }
    },
    commisTotal() {
      return this.filteredComissions.reduce((sum, detail) => {
        return parseFloat(sum) + parseFloat(detail.comis_price);
      }, 0);
    },
    showModalTurn() {
      this.$refs.turnChoose.show();
    },
    chooseTurn(value) {
      this.Commission.turn_name = value.turn_name;
      this.Commission.turn_id = value.turn_id;
    },
    turnAddSuccess(value) {
      this.Commission.turn_name = value.turn_name;
      this.Commission.turn_id = value.turn_id;
    },
    addTurn() {
      this.$refs.turnAdd.show();
    },
    totalDetails(contract) {
      console.log(contract.details);
      return contract.details.reduce(
        (sum, detail) => sum + detail.detail_quantity * detail.detail_product_price,
        0
      );
    },
    async store() {
      if (this.Commission.partner_id > 0 && this.Commission.turn_id > 0) {
        this.$confirm({
          title: "Thông báo",
          message: "Bạn chưa chọn đợt thanh toán hoặc CTV",
          button: {
            yes: "Yes",
          },
          callback: (confirm) => {},
        });
        return false;
      }
      this.$econtract.showLoading();
      delete this.Commission.turn_name;
      clearTimeout(this.debounceTimeout);

      try {
        let response;
        if (!this.isUpdate) response = await axios.post(`/commissions`, this.Commission);
        else response = await axios.put(`/commissions/update`, this.Commission);

        if (response.status == 200)
          this.$toast.success(
            !this.isUpdate
              ? "Thêm phiếu thanh toán thành công"
              : "Cập nhật phiếu thanh toán thành công"
          );

        this.$router.push({
          name: "commission-index",
        });
      } catch (error) {
        this.$toast.error("Có lỗi xảy ra trong quá trình xử lý");
      } finally {
        this.$econtract.hideLoading();
      }

      // if (!this.isUpdate) {
      //   await axios.post(`/commissions`, this.Commission).then((response) => {
      //     if (response.status == 200) {
      //       this.$toast.success("Thêm phiếu thanh toán thành công");
      //     }
      //     this.$econtract.hideLoading();
      //   });
      // } else {
      //   await axios.put(`/commissions/update`, this.Commission).then((response) => {
      //     if (response.status == 200) {
      //       this.$toast.success("Cập nhật phiếu thanh toán thành công");
      //     }
      //   });
      // }
    },
    async getPartners() {
      await axios.get(`/partners`).then((response) => {
        this.partners = response.data;
      });
    },
    async getCommissions() {
      this.$econtract.showLoading();
      await axios.get(`/commissions/${this.Commission.comis_id}`).then((respone) => {
        this.Commission = respone.data;
        this.$econtract.hideLoading();
      });
    },
    async getContracts() {
      await axios.get(`/commissions/contracts`).then((response) => {
        this.contracts = response.data.map((c) => {
          return {
            id: c.contract_id,
            text:
              c.company.company_name +
              ` ( ${this.$econtract.formatAmout(c.contract_total)} )`,
            price: c.contract_total,
          };
        });
      });
    },
  },
};
</script>
