<template>
  <b-modal
    id="modal-add-turn"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="md"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
    title="Thêm đợt thanh toán"
  >
    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Tên đợt</label>
          <input
            type="text"
            v-model="Turn.turn_name"
            class="form-control"
            placeholder="Nhập tên đợt thanh toán"
          />
        </div>
        <div class="form-group">
          <label>Ngày tạo đợt</label>
          <!-- <input type="text" v-model="Company.owner_birthday" class="form-control" placeholder="Ngày sinh" /> -->
          <date-picker v-model="Turn.turn_date" :config="options"></date-picker>
          <!-- <input type="text" v-model="Turn.turn_date" class="form-control"> -->
        </div>
      </div>
    </div>

    <template #modal-footer>
      <button @click="saveTurn" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-add-turn')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import axios from "axios";
import moment from "moment";

export default {
  components: {
    datePicker,
  },
  data() {
    return {
      Turn: {
        turn_name: "",
        turn_date: this.$econtract.formatDate(new Date()),
        turn_status : 0
      },
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
    };
  },
  methods: {
    async saveTurn() {
      await axios.post(`/turns`, this.Turn).then((response) => {
            this.$emit("turnAddSuccess",response.data);
            this.$toast.success("Thêm đợt thanh toán thành công");
            this.$bvModal.hide("modal-add-turn");
      });
    },
    show() {
      this.$bvModal.show("modal-add-turn");
    },
  },
};
</script>
