<template>
  <b-modal
    id="modal-add-contract-comission"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="xl"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
    title="Chọn hợp đồng"
  >
    <div class="tableFixHead">
      <table class="table table-striped table">
        <thead>
          <tr>
            <th>#</th>
            <th width="50%">Tên công ty</th>
            <th>Mã số thuế</th>
            <th>Trạng thái</th>
            <th>Tổng tiền</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td>
              <input
                class="form-control"
                v-model="search.company_name"
                placeholder="Tìm theo tên công ty"
              />
            </td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr v-for="(item,index) in filteredContract" :key="item.contract_id">
            <td>{{ index+1}}</td>
            <td>
              <strong class="text-primary"> {{ item.company.company_name }}</strong>
              <ul class="list-group">
                <li
                  class="list-group-item"
                  v-for="detail in item.details"
                  :key="detail.detail_id"
                >
                  {{ detail.detail_content }}
                </li>
              </ul>
            </td>
            <td>{{ item.company.company_taxcode }}</td>
            <td>
              <template v-if="item.contract_type == 1">
                <span class="badge bg-warning bg-opacity-10 text-black"
                  ><i class="fa-solid fa-circle-small"></i> Gia hạn</span
                >
              </template>
              <template v-else>
                <span class="badge bg-success bg-opacity-10 text-success"
                  ><i class="fa-solid fa-circle-small"></i> Mới</span
                >
              </template>
            </td>
            <td>
              <strong class="text-danger">{{ formatAmount(item.contract_total) }}</strong>
            </td>
            <th>
              <button class="btn btn-primary-soft btn-sm" @click="chooseContract(item)">
                <i class="fa-regular fa-circle-arrow-right"></i>
              </button>
            </th>
          </tr>
        </tbody>
      </table>
    </div>
    <NotFound v-if="contracts.length==0"></NotFound>
    <template #modal-footer>
     
      <button
        class="btn btn-secondary"
        @click="$bvModal.hide('modal-add-contract-comission')"
      >
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import NotFound from './NotFound.vue';
export default {
  components:{
    NotFound
  },  
  data() {
    return {
      contracts: [],
      search: {
        company_name: "",
      },
    };
  },
  computed: {
    filteredContract() {
      return this.contracts.filter((item) => {
        return (
          item.company.company_name
            .toLowerCase()
            .indexOf(this.search.company_name.toLowerCase()) !== -1
        );
      });
    },
  },
  methods: {
    show() {
      this.$bvModal.show("modal-add-contract-comission");
      this.getContracts();
    },
    formatAmount(amount) {
      return this.$econtract.formatAmout(amount);
    },
    async getContracts() {
      this.$econtract.showLoading();
      await axios.get(`/commissions/contracts`).then((response) => {
        this.contracts = response.data;
        this.$econtract.hideLoading();
      });
    },
    chooseContract(contract) {
      this.$emit("chooseContract", contract);
      this.$toast.success("Thêm hợp đồng thành công");
      this.$bvModal.hide("modal-add-contract-comission");
    },
  },
};
</script>
